@import url(https://fonts.googleapis.com/css?family=Josefin+Sans:400,600);
body{
    font-family: 'Josefin Sans', sans-serif!important;
}
img{
    max-width: 100%;
}

.imageWrapper{
    position: relative;
    overflow: hidden;
}
.imageWrapper .overlayContent {
    position: absolute;
    height: 100%;
    top: 0;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    -webkit-transition: 0.3s;
    transition: 0.3s;
    -webkit-transform: scale(1.2);
    transform: scale(1.2);
    opacity: 0;
    visibility: hidden;
}
.imageWrapper .overlayContent a{
    position: relative;
}
.imageWrapper .overlayContent:before {
    content: "";
    position: absolute;
    height: 100%;
    width: 100%;
    background: rgba(239, 239, 239, 0.58);
}
.singleProject{
    margin-bottom: 30px;
    box-shadow: 0 0 20px #e3ddea;
}
.singleProject:hover .imageWrapper .overlayContent{
    -webkit-transform: scale(1);
    transform: scale(1);
    opacity: 1;
    visibility: visible;
}
.singleProject:hover .imageWrapper img{
    -ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=20);
    filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=20);
    -webkit-filter: blur(2px);
            filter: blur(2px);
}
.singleProject img{
    width: 100%;
}

